import { requestApi } from '../util/network'


// 
export const getChatInfo =
    (userId,goodsId) =>
      requestApi(
        '/Memberchat/get_node_info',
        'POST',
        {
            u_id:userId,
            chat_goods_id:goodsId
        },
        'member'
      )
// 
export const addChat =
    (data) =>
        requestApi(
            '/Memberchat/send_msg',
            'POST',
            data,
            'member'
        )
// 
export const getChatHistory =
    (params,t_id) =>
        requestApi(
            '/Memberchat/get_chat_log',
            'POST',
            {
                page: params.page,
                per_page: params.per_page,
                t_id:t_id,
            },
            'member'
        )
// 
export const getChatList =
    () =>
        requestApi(
            '/Memberchat/get_user_list',
            'POST',
            {
                recent:1,
            },
            'member'
        )
//         
export const getChatCount =
    () =>
        requestApi(
            '/Memberchat/get_msg_count',
            'POST',
            {
            },
            'member'
        )