import Vue from 'vue'
import Vuex from 'vuex'

import createPersistedState from 'vuex-persistedstate'
import * as getters from './getter'

import goodsclass from './modules/goodsclass'// 
import goodsdetail from './modules/goodsdetail'// 
import pointsgoods from './modules/pointsgoods'// 
import home from './modules/home'// 
import homesearch from './modules/homesearch'// 
import homecart from './modules/homecart'// 
import member from './modules/member'// 
import seller from './modules/seller'// 
import config from './modules/config'// 

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    home,
    homesearch,
    homecart,
    goodsclass,
    goodsdetail,
    pointsgoods,
    member,
    seller,
    config
  },
  getters,
  plugins: [
    createPersistedState({
      key: 'dsmall',
      paths: ['member', 'seller', 'config', 'home', 'homecart', 'goodsdetail', 'pointsgoods']
    })
  ]
})
